	module.exports = {"data":{"post":{"html":"<p>Some days it is just better to stay in bed. Today MamaMaus was under stress at work, more than usual. But she's paid well to handle stress, and one of her perks is the new BMW 500 class TurboDiesel she drives to the office. Today she was running both late and low on fuel, so she pulled into Shell, drove up to the diesel pump, and filled up with <a title=\"Shell Tankstellen\" href=\"http://www.shell-select.de/fr_index_fl.asp?link=/pages/station/unterwegs/vpower.html\">V-Power</a>.</p>\n<p>Shell V-Power is not diesel. It's 100 octane SuperPlus gasoline.</p>\n<p>A few minutes later, her onboard computer reported 'Motor Damage', and this time it was not an error. The repair will cost 4000 Euros and will take at least a week. During that time she'll be driving a loaner <a title=\"smart.com - roadster\" href=\"http://www.smart.de/-snm-0135155096-1068460332-0000026314-0000000004-1068752455-enm-smart/content/de/de/smart/modelle/smartroadster\">Smart Roadster</a>, which is easily a half-size smaller than my Twingo. And worst of all, her boss tomorrow is going to have a huge laugh at her expense.</p>","fields":{"title":"Roadster","date":"13 Nov 2003","url":"https://papascott.de/2003-11-13-roadster/"}}},"pathContext":{"slug":"/2003-11-13-roadster/","prev":{"fields":{"title":"McDonald's ist online mit SUSE LINUX","date":"13 Nov 2003","slug":"/2003-11-13-mcdonalds-ist-online-mit-suse-linux/"}},"next":{"fields":{"title":"Security snake oil","date":"13 Nov 2003","slug":"/2003-11-13-security-snake-oil/"}}}}

